<template>
  <nav class="navbar navbar-dark bg-dark" aria-label="First navbar example">
    <div class="container-fluid d-flex">
      <div class="flex-fill">
       <button class="btn" @click="redirectToMap(isMap)">
         <i class="far fa-map text-white fs-5"></i>
       </button>
      </div>
      <div class="flex-fill">
        <a class="navbar-brand mx-0" href="#">1944 Monte Cassino</a>
      </div>
      <div class="flex-fill">
        <button class="navbar-toggler collapsed border-0 px-0" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarsExample01" aria-controls="navbarsExample01" aria-expanded="false"
                aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
      </div>

      <div class="navbar-collapse collapse" id="navbarsExample01" style="">
        <ul class="navbar-nav me-auto mb-2">
          <li class="nav-item">
          </li>
        </ul>

      </div>
    </div>
  </nav>
</template>
<script>
export default {
  data() {
    return {
      isMap: false
    }
  },
  methods: {
    redirectToMap(value) {
      this.isMap = !value;
      this.$store.commit('setMobileMap',this.isMap );
      this.runPreloader();
    },
    runPreloader() {
      let loader = this.$loading.show(
              {
                backgroundColor: '#000',
                opacity: 1,
                color: '#fff',
                loader: 'dots'
              }
      );
      setTimeout(() => {
        loader.hide();
      }, 1000);
    }
  }
}
</script>
<style scoped>
.navbar-toggler {
  border-color: transparent !important;
}
</style>